<template>
	<div>
		<van-nav-bar class="hader" :title="activeinfo.name" left-text="主页" left-arrow @click-left="back()">
			<a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS != true && isWeixin != true"><van-button type="primary" size="small">安装APP</van-button></a>
			<a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS == true && isWeixin != true">
				<van-button type="primary" size="small">安装APP</van-button>
			</a>
			<a href="https://mp.weixin.qq.com/mp/profile_ext?action=home&__biz=MzU0NTg5ODQzNA==&scene=124#wechat_redirect" slot="right" v-if="isWeixin == true">
				<van-button type="primary" size="small">关注</van-button>
			</a>
		</van-nav-bar>

		<div class="ac-info" style="margin-top: 3.5rem;">
			<img v-for="(item, index) in activeinfo.content" :key="index" :style="item.imgstyle" :src="osshost + item.imgurl" @click="navToDetail(item.navtype, item.navid)" />
		</div>
		<div class="cont mt10" style="margin-top: 0.5rem;">
			<!--左边区域-->
			<div v-if="activeinfo.status == 2">
				<a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS != true && isWeixin != true">
					<van-button class="wb100 mt10 mb10" type="primary">去APP参与活动</van-button>
				</a>
				<a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS == true && isWeixin != true">
					<van-button class="wb100 mt10 mb10" type="primary">去APP参与活动</van-button>
				</a>
				<div v-if="isWeixin == true" class="gzh-guanzhu">
					<img src="../assets/img/qr.jpg" />
					<span>长按识别二维码，关注管饭美食参与活动</span>
				</div>
			</div>
			<div v-else>
				<a href="https://m.guanfan.top/#/app" slot="right" v-if="isiOS != true && isWeixin != true">
					<van-button class="wb100 mt10 mb10" type="primary">去APP参与更多活动</van-button>
				</a>
				<a href="https://apps.apple.com/cn/app/id1487127641" slot="right" v-if="isiOS == true && isWeixin != true">
					<van-button class="wb100 mt10 mb10" type="primary">去APP参与更多活动</van-button>
				</a>
				<div v-if="isWeixin == true" class="gzh-guanzhu">
					<img src="../assets/img/qr.jpg" />
					<span>关注管饭美食参与更多活动</span>
				</div>
			</div>
			<!--封面图-->

			<!--用户信息-->
			<div v-if="activeinfo.sponsorid != 0">
				<h3 class="van-doc-demo-block__title mt15 mb15">赞助商</h3>
				<ul class="dr_list">
					<li class="clearfix mb15">
						<div class="left headicon bg-img br50" :style="{ backgroundImage: 'url(' + osshost + userinfo.imgurl + userface + ')' }"></div>
						<div class="info">
							<h2 class="text-bold text-df text-16">{{ userinfo.nickname }}</h2>
						</div>
						<div class="info2">
							<h3 class="text-bold text-df text-14">{{ userinfo.personalprofile }}</h3>
						</div>
					</li>
				</ul>
			</div>
			<!--  推荐菜谱    -->
			<h3 class="van-doc-demo-block__title mt15 mb15">饭友参与作品</h3>
			<LoadMore :onLoadMore="onLoadMore" :enableLoadMore="enableLoadMore">
				<div class="food-list1 food-list2" v-if="activeinfo.type == 1">
					<!-- <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load"> -->
					<ul>
						<li v-for="(item, index) in foodList" :key="index">
							<div class="foodlist-img" :style="{ backgroundImage: 'url(' + osshost + item.dtimg + listpic + ')' }" @click="navToDetail(1, item.id)"></div>
							<div class="food-list1-tit text-cut">{{ item.description }}</div>
							<div class="user">
								<img :src="imgurl" v-if="!item.faceimg">
								<img :src="osshost + item.faceimg +userface" v-if="item.faceimg&&item.isThree==false">
								<img :src="item.faceimg" v-if="item.faceimg&&item.isThree==true">
								<span>{{ item.nickname }}</span>
							</div>
							<i class="icon-like van-icon van-icon-good-job" :class="item.isliked==1?'text-red':''"  @click="changeLike(item.id,index)">{{ item.likecount }}</i>
						</li>
					</ul>
					<!-- </mu-load-more> -->
				</div>
				<div class="food-list1 food-list2" v-if="activeinfo.type == 2">
					<!-- <mu-load-more @refresh="refresh" :refreshing="refreshing" :loading="loading" @load="load"> -->
					<ul>
						<li v-for="(item, index) in menuList" :key="index" >
							<div class="foodlist-img" :style="{ backgroundImage: 'url(' + osshost + item.cpimg + listpic + ')' }" @click="navToDetail(2, item.cpid)"></div>
							<div class="food-list1-tit text-cut">{{ item.cptitle }}</div>
							<div class="user">
								<img :src="imgurl" v-if="!item.faceimg">
								<img :src="osshost + item.faceimg +userface" v-if="item.faceimg&&item.isThree==false">
								<img :src="item.faceimg" v-if="item.faceimg&&item.isThree==true">
								<span>{{ item.nickname }}</span>
							</div>
							<i class="icon-like van-icon van-icon-like" :class="item.iscollectioned==1?'text-red':''" @click="changeCollection(item.cpid,index)">{{ item.collections }}</i>
						</li>
					</ul>
					<!-- </mu-load-more> -->
				</div>
			</LoadMore>
		</div>
	</div>
</template>

<script>
	import qs from 'qs';
	import axios from 'axios';
import LoadMore from '../components/LoadMore.vue';
export default {
	name: 'activeinfo',
	components: {
		LoadMore
	},
	data() {
		return {
			num: 13,
			sortList: '',
			activeinfo: '',
			userinfo: '',
			osshost: 'http://guanfan.oss-cn-hangzhou.aliyuncs.com/',
			// osshost: 'http://guanfan.oss-cn-hangzhou.aliyuncs.com/',
			listpic: '?x-oss-process=style/list-img',
			userface: '?x-oss-process=style/user-img',
			detailpic: '?x-oss-process=style/detail-img',
			isWeixin: '',
			isiOS: '',
			uid: '',
			enableLoadMore: true,
			page: 1,
			type: '',
			foodList: [],
			menuList: [],
			token:'',
			imgurl: '../assets/img/userIcon.png',
			isThree: false
		};
	},
	created() {
		this.aid = this.$route.query.id;
		this.token = this.$store.state.user.token || '';
		this.getDataInfo(this.aid);
		this.getwx();
		this.appSource();
	},
	methods: {
		getwx() {
			var ua = window.navigator.userAgent.toLowerCase();
			if (ua.match(/MicroMessenger/i) == 'micromessenger') {
				this.isWeixin = true;
			} else {
				this.isWeixin = false;
			}
		},
		appSource() {
			const u = navigator.userAgent,
				app = navigator.appVersion;
			if (!!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)) {
				this.isiOS = true;
			} else {
				this.isiOS = false;
			}
		},
		onLoadMore(done) {
			setTimeout(() => {
				if (!this.enableLoadMore) {
					return;
				}
				this.page = this.page + 1;
				if (this.activeinfo.type == 1) {
					this.getDtList();
				}
				if (this.activeinfo.type == 2) {
					this.getMenUList();
				}
				done();
			}, 500);
		},
		getDataInfo(aid) {
					let data = qs.stringify({
						campaignid: aid
					});
					axios({
						method: 'post',
						url: '/campaign/detail',
						headers: {
							'Content-Type': 'application/x-www-form-urlencoded',
							'token':this.token
						},
						data
					}).then(response => {
						if (response.data.success) {
							this.activeinfo = response.data.result;
							this.uid = this.activeinfo.sponsorid;
							this.getUserInfo(this.uid);
							if (this.activeinfo.type == 1) {
								this.getDtList();
							} else {
								this.getMenUList();
							}
						}
					});
		},
		getUserInfo(uid) {
			// getSponinfo(uid).then(response => {
			// 	if (response.data.success) {
			// 		this.userinfo = response.data.result;
			// 	}
			// });
			let data = qs.stringify({
				sponsorid: uid
			});
			axios({
				method: 'post',
				url: '/campaign/sponsor',
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded'
				},
				data
			}).then(response => {
				if (response.data.success) {
					this.userinfo = response.data.result;
				}
			});
		},
		getDtList() {
			let data = qs.stringify({
				page:this.page,
				campaignid:this.aid,
				ctype:1
			});
			axios({
				method: 'post',
				url: '/campaign/dtlist',
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'token':this.token
				},
				data
			}).then(response => {
				if (response.data.success) {
					if (response.data.result.length < 10) {
						this.enableLoadMore = false;
					}
					this.foodList = this.foodList.concat(response.data.result);
					for (var i = 0; i < this.foodList.length; i++) {
						this.foodList[i].dtimg = this.foodList[i].dtimg.split('|');
						this.foodList[i].dtimg = this.foodList[i].dtimg[0];
						this.foodlist[i].isThree = new RegExp('http').test(this.foodlist[i].faceimg);
					}
				}
			});
		},
		getMenUList() {
			// getCamCplist(this.page, this.aid, 1).then(response => {
			// 	if (response.data.success) {
			// 		if (response.data.result.length < 10) {
			// 			this.enableLoadMore = false;
			// 		}
			// 		this.menuList = this.menuList.concat(response.data.result);
			// 	}
			// });
			let data = qs.stringify({
				page:this.page,
				campaignid:this.aid,
				ctype:1
			});
			axios({
				method: 'post',
				url: '/campaign/cplist',
				headers: {
					'Content-Type': 'application/x-www-form-urlencoded',
					'token':this.token
				},
				data
			}).then(response => {
				if (response.data.success) {
					if (response.data.result.length < 10) {
						this.enableLoadMore = false;
					}
					this.menuList = this.menuList.concat(response.data.result);
					for (var i = 0; i < this.menuList.length; i++) {
						this.menuList[i].isThree = new RegExp('http').test(this.menuList[i].faceimg);
					}
				}
			});
		},
		navToDetail(type, id) {
			if (type == 1) {
				this.$router.push({ path: '/dtinfo', query: { id: id } });
			} else if (type == 2) {
				this.$router.push({ path: '/cookbookinfo', query: { id: id } });
			} else if (type == 3) {
				this.$router.push({ path: '/themeinfo', query: { id: id } });
			} else if (type == 4) {
				return;
			}
		},
		back() {
			this.$router.push({ path: '/' });
		},
		// changeLike(dtid,i){
		// 	if(this.token==""){
		// 			this.$router.push({ path: '/pswLogin'});
		// 	}else{
		// 		if(this.foodList[i].isliked==1){
		// 			setUnLike(dtid,1).then(res=>{
		// 				if(res.data.success){
		// 					this.foodList[i].isliked=0;
		// 				    this.foodList[i].likecount--
		// 				}
		// 			})
		// 		}else{
		// 			setLike(dtid,1).then(res=>{
		// 				if(res.data.success){
		// 					this.foodList[i].isliked=1;
		// 				    this.foodList[i].likecount++
		// 				}
		// 			})
		// 		}
		// 	}
		// },
		changeLike(dtid,i){
			if(this.token==""){
					this.$router.push({ path: '/pswLogin'});
			}else{
				if(this.foodList[i].isliked==1){
							let data = qs.stringify({
								itemid:dtid,
								itemtype:1
							});
							axios({
								method: 'post',
								url: '/action/unlike',
								headers: {
									'Content-Type': 'application/x-www-form-urlencoded',
									'token':this.token
								},
								data
							}).then(response => {
								if (response.data.success) {
									this.foodList[i].isliked=0;
									this.foodList[i].likecount--
								}
							});
				}else{
							let data = qs.stringify({
								itemid:dtid,
								itemtype:1
							});
							axios({
								method: 'post',
								url: '/action/like',
								headers: {
									'Content-Type': 'application/x-www-form-urlencoded',
									'token':this.token
								},
								data
							}).then(response => {
								if (response.data.success) {
									this.foodList[i].isliked=1;
									this.foodList[i].likecount++
								}
							});
				}
			}
		},
		
		changeCollection(cpid,i){
			if(this.token==""){
					this.$router.push({ path: '/pswLogin'});
			}else{
				if(this.menuList[i].iscollectioned==1){
							let data = qs.stringify({
								itemid:cpid,
								itemtype:2
							});
							axios({
								method: 'post',
								url: '/action/uncollection',
								headers: {
									'Content-Type': 'application/x-www-form-urlencoded',
									'token':this.token
								},
								data
							}).then(response => {
								if (response.data.success) {
									this.menuList[i].iscollectioned=0;
									this.menuList[i].collections--
								}
							});
				}else{
							let data = qs.stringify({
								itemid:cpid,
								itemtype:2
							});
							axios({
								method: 'post',
								url: '/action/collection',
								headers: {
									'Content-Type': 'application/x-www-form-urlencoded',
									'token':this.token
								},
								data
							}).then(response => {
								if (response.data.success) {
									this.menuList[i].iscollectioned=1;
									this.menuList[i].collections++
								}
							});
				}
			}
		},
	}
};
</script>

<style>
.food-img img {
	width: 100%;
}
.dt-dz {
	width: 100%;
	height: 2rem;
	margin-top: 1rem;
}
.dt-dz-l {
	width: 5rem;
	line-height: 2rem;
	font-size: 0.8rem;
	color: #333;
	text-align: left;
	float: left;
}
.dt-dz-r {
	float: right;
}
.dt-dz-r span {
	width: 2rem;
	height: 2rem;
	display: block;
	float: left;
	background-position: center;
	background-size: 100%;
	border-radius: 100%;
	border: 1px solid #eee;
	margin-left: -0.5rem;
}
.info2 {
	width: 100%;
	min-height: 20px;
	overflow: hidden;
	clear: both;
	padding-top: 8px;
}
.van-icon-like:before{
	display: inline-block;
	position: relative;
}
</style>
